import React from 'react';
import logo from './logo.svg';
import './App.css';

const regexName = /^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/;
//const regexMail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

class NameForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      lastname: '',
      nameError: false,
      lastnameError: false
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);


  }
  handleInputChange(event) {
    const target = event.target;
    const value = target.value;

    
    switch (target.name) {
      case "name":
        this.setState({ name: value });
        if (!regexName.test(value)) {
          this.state.nameError = true;
        } else {
          this.state.nameError = false;
        }
        break;
      case "lastname":
        this.setState({ lastname: value });
        if (!regexName.test(value)) {
          this.state.lastnameError = true;
        } else {
          this.state.lastnameError = false;
        }
        break;
      
      default:
        //Declaraciones ejecutadas cuando ninguno de los valores coincide con el valor de la expresi√≥n
        break;
    }
   
    
  }

  handleSubmit(event) {
    if (!this.state.nameError || !this.state.lastnameError) {
      alert('A name was submitted: ' + this.state.name);
      event.preventDefault();

    }
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Name:
          <input
            name="name"
            className={(this.state.nameError == true ? "invalid" : "valid")}
            type="text"
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <label>
          Lastname:
          <input
            name="lastname"
            className={(this.state.lastnameError == true ? "invalid" : "valid")}
            type="text"
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <input type="submit" value="Submit" />
      </form>
    );
  }
}



export default NameForm;
